          selectedIconPath: '/static/game-active.png'
        },
        {
          pagePath: '/pages/profile/profile',
          text: '我的',
          iconPath: '/static/profile.png',
          selectedIconPath: '/static/profile-active.png'
        }
      ]
    }
  },
  methods: {
    handleTabChange(index) {
      uni.switchTab({
        url: this.tabList[index].pagePath
      })
    }
  }
}
</script>

